/* General Styles for Doctor Dashboard */
.doctor-dashboard-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(135deg, #ff7eb3, #ff758c);
    color: #ffffff;
    border-radius: 12px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
}

.doctor-dashboard-container h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5);
}

.patient-list {
    width: 100%;
    max-width: 800px;
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
}

.patient-list h2 {
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 15px;
    text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.4);
}

.patient-list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.patient-list li {
    padding: 10px 15px;
    background: rgba(255, 255, 255, 0.2);
    margin-bottom: 10px;
    border-radius: 8px;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.patient-list li:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.4);
}

.patient-link {
    color: #ffffff;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    display: block;
}

.patient-link:hover {
    color: #ffe4e4;
}

/* Responsive Design */
@media (max-width: 768px) {
    .doctor-dashboard-container h1 {
        font-size: 2rem;
    }

    .patient-list {
        padding: 15px;
    }

    .patient-list li {
        padding: 8px 10px;
    }
}