/* General Styles for Patient Dashboard */
.patient-dashboard-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background: linear-gradient(135deg, #6a11cb, #2575fc);
    color: #ffffff;
    border-radius: 12px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
}

.patient-dashboard-container h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5);
}

.records-list {
    width: 100%;
    max-width: 800px;
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
}

.records-list h2 {
    text-align: center;
    font-size: 1.8rem;
    margin-bottom: 15px;
    text-shadow: 0px 3px 5px rgba(0, 0, 0, 0.4);
}

.records-list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.record-item {
    padding: 10px 15px;
    background: rgba(255, 255, 255, 0.2);
    margin-bottom: 10px;
    border-radius: 8px;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.record-item:hover {
    transform: translateY(-5px);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.4);
}

.record-item div {
    margin-bottom: 5px;
}

/* Responsive Design */
@media (max-width: 768px) {
    .patient-dashboard-container h1 {
        font-size: 2rem;
    }

    .records-list {
        padding: 15px;
    }

    .record-item {
        padding: 8px 10px;
    }
}
