/* General Styles for Transfer Page */
.transfer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: linear-gradient(135deg, #6a11cb, #2575fc);
    color: #ffffff;
    padding: 20px;
}

.transfer-form {
    background: rgba(255, 255, 255, 0.1);
    padding: 25px 35px;
    border-radius: 12px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    max-width: 400px;
}

.transfer-form h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 2rem;
    text-shadow: 0px 3px 6px rgba(0, 0, 0, 0.5);
}

.form-group label {
    font-size: 1rem;
    margin-bottom: 5px;
    display: block;
    font-weight: bold;
}

.form-group select {
    padding: 10px;
    font-size: 1rem;
    border: none;
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.8);
    color: #333;
    outline: none;
    transition: box-shadow 0.3s ease;
}

.form-group select:focus {
    box-shadow: 0px 0px 5px rgba(255, 255, 255, 0.7);
}

.message {
    text-align: center;
    font-weight: bold;
    margin-top: -10px;
    padding: 10px;
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.2);
}

.transfer-button {
    padding: 12px;
    font-size: 1rem;
    font-weight: bold;
    background: linear-gradient(135deg, #4facfe, #00f2fe);
    color: #ffffff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: transform 0.2s ease, background 0.3s ease;
}

.transfer-button:hover {
    transform: translateY(-3px);
    background: linear-gradient(135deg, #00f2fe, #4facfe);
}

.transfer-button:active {
    transform: translateY(0);
}

/* Responsive Design */
@media (max-width: 768px) {
    .transfer-container {
        padding: 15px;
    }

    .transfer-form {
        padding: 20px 25px;
    }

    .transfer-form h1 {
        font-size: 1.8rem;
    }
}